import React from 'react'
import { withRouteData } from 'react-static'
import styled from 'styled-components'
import PostContent from '../components/post-content'
import { sliceStrByCharNumber, isEn, markdownToText } from '../util'
import invert from 'lodash.invert'

const tag_name_map = invert({
  '前端': 'frontend',
  '后端': 'backend',
  '开发': 'development',
  '移动端': 'mobile',
  '跨平台': 'cross platform'
})

export default withRouteData(({posts, tag}) => {
  return (
    <Root>
      <Tag>
        <h2>{isEn(tag) ? (tag_name_map[tag] || `${tag[0].toUpperCase()}${tag.slice(1)}`) : tag}</h2>
      </Tag>
      <List>
        {
          posts.map((v, i) => (
            <PostContent
              key={i}
              caption={{
                readingtime: v.readingtime,
                date: v.date
              }}
              title={v.title}
              summary={sliceStrByCharNumber(v.summary, 320)}
              url={v.url}
            />
          ))
        }
      </List>
    </Root>
  )
})

const Root = styled.div`
  width: 80%;
  max-width: 1000px;
  margin: 3rem auto;

  > div {
    margin-bottom: 3rem;
  }

`

const Tag = styled.div`
  padding: 30px 0;
  margin: 30px 0;
  border-bottom: 1px solid #e1e1e1;
`

const List = styled.div`
  display: grid;
  grid-template-columns: minmax(auto, 400px) minmax(auto, 400px);
  grid-row-gap: 3rem;
  grid-column-gap: 3rem;
  justify-content: space-between;
`



