import styled from 'styled-components'

export default styled.article`
  position: relative;
  width: 80%;
  max-width: 740px;
  margin: 4rem auto;
  padding-bottom: 4rem;
  border-bottom: 1px solid #ebf2f6;
  word-wrap: break-word

  &::after {
    display: block;
    content: "";
    width: 7px;
    height: 7px;
    border: 1px solid #e7eef2;
    position: absolute;
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    background: #fff;
    border-radius: 100%;
    box-shadow: 0 0 0 5px #fff
  }

  .read-more {
    text-decoration: none;
  }

  .author-thumb {
    width: 24px;
    height: 24px;
    float: left;
    margin-right: 9px;
    border-radius: 100%;
  }

  .post-header {
    margin-bottom: 3.4rem;
  }

  .post-title a {
    text-decoration: none
  }

  .post-meta {
    display: block;
    margin: 1.75rem 0 0;
    font-family: Open Sans,sans-serif;
    font-size: 1.5rem;
    line-height: 2.2rem;
    color: #9eabb3
  }

  .post-excerpt p {
    margin: 0;
    font-size: .9em;
    line-height: 1.7em
  }

  .post-meta a {
    color: #9eabb3;
    text-decoration: none
  }

  .post-meta a:hover {
    color: #9eabb3;
    text-decoration: underline
  }

  .post-date {
    display: inline-block;
    text-transform: uppercase;
    font-size: 1.3rem;
    white-space: nowrap;
    &.has-author {
      margin-left: 8px;
      padding-left: 12px;
      border-left: 1px solid #d5dbde;
    }
  }

  @media only screen and (max-width: 900px) {
    font-size:.95em
  }

  @media only screen and (max-width: 500px) {
    width:auto;
    margin: 2rem 16px;
    padding-bottom: 2rem;
    line-height: 1.65em;

    .post-excerpt p {
      font-size: .85em;
    }

    .post-meta {
        font-size: 1.3rem;
        margin-top: 1rem
    }

    pre {
      padding: 0!important;
    }
  }

  pre {
    padding: 1.2rem;
    > code {
      overflow-x: auto;
      box-shadow: 0 0 15px rgba(0,0,0,.35);
      padding: 1.5rem;
    }
  }

  blockquote {
    border-left: 3px solid #061e35!important;
    margin-left: -23px!important;
    padding-bottom: 2px!important;
    padding-left: 20px!important;
    color: #24292e!important;
    p {
      color: #24292e!important;
    }
    a {
      color: #061e35!important;
    }
  }
`



