import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-static'
import cx from 'classnames'
import moment from 'moment'

function getCateLink(cate) {
  return cate === '前端' ? `/tags/Frontend` : cate === '后端' ? `/tags/Backend` : cate === '移动端' ? `tags/Mobile` : `tags/Others`
}

export default ({
  author,
  readingtime,
  featured,
  cate,
  star,
  date = moment().format('YYYY-MM-DD')
}) => (
  <Root>
    {
      cate && author && (
        <div className="cate">
          <a>
            <span>{author}</span>
          </a>
          <span style={{padding: '0 5px'}}>发表在</span>
          <Link to={getCateLink(cate)} className="cate-name">
            <span>{cate}</span>
          </Link>
        </div>
      )
    }
    <div>
      {
       (!author || (author && cate))  && [
          <span data-for="tip-two" data-tip="更新时间" key="date">{date}</span>,
          <span className="dot" key="dot"></span>
        ]
      }
      {
        author && !cate && [
          <a key="author">
            <span>{author}</span>
          </a>,
          <span className="dot" key="dot"></span>
        ]
      }
      {
        (featured || star) && (
          <span className="star">
            <svg width="15" height="15">
              <path
                d="M7.438 2.324c.034-.099.09-.099.123 0l1.2 3.53a.29.29 0 0 0 .26.19h3.884c.11 0 .127.049.038.111L9.8 8.327a.271.271 0 0 0-.099.291l1.2 3.53c.034.1-.011.131-.098.069l-3.142-2.18a.303.303 0 0 0-.32 0l-3.145 2.182c-.087.06-.132.03-.099-.068l1.2-3.53a.271.271 0 0 0-.098-.292L2.056 6.146c-.087-.06-.071-.112.038-.112h3.884a.29.29 0 0 0 .26-.19l1.2-3.52z"
              />
            </svg>
          </span>
        )
      }
      {
        (readingtime && !star) && <span title={`${readingtime}分钟阅读`} className="readingtime"></span>
      }
      {
        cate && !author && [
          <span key="comma" className="comma">,</span>,
          <Link to={getCateLink(cate)} key="cate" className="cate cate-name">
            <span>{cate}</span>
          </Link>
        ]
      }
    </div>
  </Root>
)

const fontSize = 14
const fontWeight = 400

const Root = styled.div`
  font-size: ${fontSize}px;
  line-height: 20px;
  color: rgba(0, 0, 0, .54);
  fill: rgba(0, 0, 0, .54);
  font-weight: ${fontWeight};

  > div {
    display: flex;
    align-items: center;
  }

  span {
    display: inline-block;
  }

  a {
    font-weight: ${fontWeight};
    font-size: 14px;
    color: rgba(0, 0, 0, .54);
  }

  .comma {
    margin-right: 4px;
  }

  div.cate {
    margin-top: 12px;
  }

  .cate,
  .cate a {
    color: rgba(0, 0, 0, .84);
  }

  /*
  .cate-name {
    color: rgb(57, 111, 176)!important;
  }
  */

  .dot {
    ::after {
      content: '\00B7';
    }

    padding: 0 0.5rem;
  }

  .star {
    margin-right: 4px;
    display: flex;
  }

  .readingtime {
    white-space: nowrap;
    ::after {
      content: attr(title);
    }
  }
`



