import React from 'react'
import styled from 'styled-components'
import PostCaption from './post-caption'
import { Link } from 'react-static'
import cx from 'classnames'

export default ({
  caption,
  title,
  summary,
  large,
  small,
  className,
  url = '#'
}) => (
  <Root
    className={cx({small, large, [className]: className})}
  >
    {
      caption.featured && <div className="post-nav"><div>精选文章</div></div>
    }
    <div className="title-summary">
      <Link prefetch to={url} className="title-wrap">
        <p className="title">{title}</p>
      </Link>
      {
        summary &&
        <Link to={url} className="summary-wrap">
          <p className="summary">{summary}</p>
        </Link>
      }
    </div>
    <PostCaption
      {...caption}
    />
  </Root>
)

const Root = styled.div`
  .post-nav {
    top: -1px;
    position: relative;
    padding-bottom: 16px
    display: flex
    line-height: 20px;
    > div {
      font-weight: 400;
      font-style: normal;
      font-size: 1.4rem;
      line-height: 20px;
      transform: translateY(1.8px);
      letter-spacing: .06rem;
      text-transform: uppercase;
      color: rgba(0,0,0,.76);
      border-top: 2px solid rgba(0,0,0,.84);
      padding-top: 8px;
    }
  }

  p {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .title-wrap {
    color: rgba(0, 0, 0, .84);
    font-weight: 500;
    width: 100%;
    &:hover {
      color: rgba(0, 0, 0, .84);
    }
  }

  .summary-wrap {
    color: rgba(0, 0, 0, .54);
    font-weight: 400;
    &:hover {
      color: rgba(0, 0, 0, .54);
    }
  }

  .title {
    line-height: 24px;
    font-size: 18px;
  }

  .summary {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 4px;
  }

  .title-summary {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow: hidden;
  }

  @media screen and (max-width: 1383px) {
    .summary-title {
      max-height: 88px;
    }
  }

  @media screen and (max-width: 1207px) {
    .summary-title {
      max-height: 100px;
    }
  }

  &.small {
    .title {
      font-size: 16px;
      line-height: 20px;
      color: rgba(0, 0, 0, .84);
      font-weight: 600;
    }
  }

  &.large {
    .title-summary {
      margin-bottom: 16px;
    }

    p {
      -webkit-line-clamp: 3;
    }

    .title {
      line-height: 28px;
      font-size: 20px;
      max-height: 84px;
      margin-bottom: 8px;
    }

    .summary {
      font-size: 16px;
      line-height: 24px;
      max-height: 72px;
    }

    @media screen and (max-width: 1383px) {
      .title-summary {
        max-height: 120px;
      }
    }

    @media screen and (max-width: 1031px) {
      .title-summary {
        max-height: 80px;
      }
    }

    @media screen and (max-width: 855px) {
      .title-summary {
        max-height: 80px;
      }
    }

    @media screen and (max-width: 767px) {
      .title {
        max-height: 72px;
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
`



