import React from 'react'
import { Router, Link }  from 'react-static'
import { hot } from 'react-hot-loader'
import Routes from 'react-static-routes'
import './app.css'
import styled, {keyframes} from 'styled-components'
import Headroom from 'react-headroom'
import cx from 'classnames'
import Helmet from 'react-helmet'
import ReactTooltip from 'react-tooltip'
import { backtop } from './util'
import ClickOutside from 'react-click-outside'
import Logo from './logo.png'
import LogoWhite from './codemore_white.png'
import Header from './components/header'
import Nav from './Nav'
import favicon from './favicon.ico'
import Search from './components/search'
import throttle from 'lodash.throttle'
import isEmpty from 'lodash.isempty'

const is_mobile = () => typeof window !== 'undefined' && 'ontouchstart' in window

const description = `这是一个互联网相关的技术类博客、开发笔记，提供全面的、前沿的计算机前端、后端教程，包括最新的国外技术文章，掌握最新的互联网技术资讯、趋势`
const keywords = ['前端', '后端', 'Java', 'Javascript', 'js', '开发', '教程', '技术']
const title = `代码拾遗 - 更懂代码的媒体`
const type = `文章`

class App extends React.Component {
  state = {
    isMobile: false,
    showSearch: false,
    pin: false,
    search_match_tags: null,
    search_active_index: null
  }

  toggleSearch() {
    this.setState({
      showSearch: !this.state.showSearch
    })
  }

  bindEscClose = e => {
    const len = this.state.search_match_tags && this.state.search_match_tags.length
    const { search_active_index } = this.state

    if (e.which === 27) this.handleSearch()

    if (!len) return

    // up
    else if (e.which === 38) {
      if (search_active_index === null) return
      else if (search_active_index > 0) {
        this.setState(prev => ({
          ...prev,
          search_active_index: search_active_index - 1
        }))
      }
    }
    // down
    else if (e.which === 40) {
      if (search_active_index === null) {
        this.setState({
          search_active_index: 0
        })
      }
      else if (search_active_index === len - 1) return

      else {
        this.setState(prev => ({
          ...prev,
          search_active_index: search_active_index + 1
        }))
      }
    }
  }

  handleKeyPress = e => {
    if (e.which === 13) {
      console.log(this.props)
    }
  }

  handleSearch = () => {
    this.setState({
      showSearch: !this.state.showSearch
    })

    if (!this.state.showSearch) {
      document.addEventListener('keyup', this.bindEscClose)
      document.body.classList.add('no-scroll')
    } else {
      document.removeEventListener('keyup', this.bindEscClose)
      document.body.classList.remove('no-scroll')
      this.setState({
        search_match_tags: null
      })
    }
  }

  handleSearchByKeyword = keyword => {
    console.log(`search by keyword: ${keyword}`)
  }

  handleSearchByTag = tag => {
    console.log(`search by tag: ${tag}`)
  }

  handleSearchInputChange = throttle((value, tags) => {
    const search_match_tags = value ? tags.filter(tag => tag.match(new RegExp(value.trim(), 'i'))) : null

    this.setState({
      search_match_tags
    })
  }, 600)

  handleSearchInputKeyUp = (e, navigateTagPage, navigateSearchPage) => {
    // delete
    if (e.which === 8) {
      this.setState({
        search_active_index: 0
      })
    }

    // enter
    else if (e.which === 13) {
      if (!isEmpty(this.state.search_match_tags)) {
        navigateTagPage()
        this.setState(prev => ({
          ...prev,
          search_active_index: 0
        }))
      } else {
        const { value } = e.target
        if (value) {
          navigateSearchPage()
        }
      }
    }
  }

  componentDidMount() {
    if (is_mobile()) {
      this.setState({
        isMobile: true,
      })
    } else {
      this.setState({
        isPc: true
      })
    }

    let mainNav
    let navParent
    let nav_fixed
    let sidebar_fixed
    let sidebar
    let fixed_x
    let sidebar_top
    let nav_top

    setTimeout(() => {
      window.addEventListener('scroll', () => {
        if (mainNav || (mainNav = document.querySelector('.main-nav'))) {
          nav_top = mainNav.getBoundingClientRect().top
          navParent = navParent || mainNav.parentNode

          if (nav_top <= 0 && !nav_fixed) {
            navParent.classList.add('fixed')
            nav_fixed = true
          }

          if (window.scrollY === 0 && nav_fixed) {
            navParent.classList.remove('fixed')
            nav_fixed = false
          }
        }

        if ((sidebar = document.querySelector('.sidebar-wrapper'))) {
          sidebar_top = sidebar.getBoundingClientRect().top

          if (sidebar_top <= 50  && !sidebar_fixed) {
            sidebar.classList.add('fixed')
            sidebar_fixed = true
          }

          if (window.scrollY <= fixed_x && sidebar_fixed) {
            sidebar.classList.remove('fixed')
            sidebar_fixed = false
            fixed_x = undefined
          }

          if (sidebar_fixed && !fixed_x) {
            fixed_x = window.scrollY
          }
        }
      })
    }, 0)
  }

  render() {
    return (
      <Router>
        <Root className={cx({
          'is-mobile': this.state.isMobile,
          'is-pc': this.state.isPc
        })}>
          <Helmet>
            <meta property="og:description" content={description} />
            <meta property="og:title" content={title} />
            <meta property="og:type" content={type} />
            <meta name="title" content={title} />
            <meta name="description" content={description} />
            <meta name="keywords" content={keywords.join(',')} />
            <link rel="shortcut icon" type="image/x-icon" href={favicon} />
            <title>{title}</title>
          </Helmet>
          <Header>
            <div style={{maxWidth: 1000,margin: '0 auto', background: '#fff', height: 65, position: 'relative'}}>
              <Title className="logo"></Title>
            </div>
            <Nav onSearch={this.handleSearch} />
          </Header>
          <div style={{height: 115}}></div>
          <Content>
            <Routes />
          </Content>
          {
            !is_mobile() && [
              <ReactTooltip key="tip-one" effect="solid" place="left" id="tip-one" />,
              <ReactTooltip key="tip-two" effect="solid" place="top" id="tip-two" />,
            ]
          }
          {
            this.state.showSearch && (
              <Search
                close={this.handleSearch}
                onSearchInputChange={this.handleSearchInputChange}
                searchByTag={this.handleSearchByTag}
                searchByKeyword={this.handleSearchByKeyword}
                search_match_tags={this.state.search_match_tags}
                activeItem={this.state.search_active_index}
                onSearchInputKeyUp={this.handleSearchInputKeyUp}
              />
            )
          }
        </Root>
      </Router>
    )
  }
}

export default hot(module)(App)

const Root = styled.div``

const Content = styled.div`
  &::after {
    content: '';
    display: table;
  }

  @media screen and (max-width: 717px) {
    padding: 1rem;
  }
`

const SlideRightIn = keyframes`
  0% {
    width: 0px;
    padding: 4px 0;
  }

  100% {
    width: 160px;
    padding: 4px 5px;
  }
`
const ToggledPopular = styled.span`
  display: none;
  padding: 0;
  margin: 0;
  position: relative;
  font-size: 1.4rem;

  &::before {
    content: '';
    height: 2.1rem;
    width: 1px;
    background-color: #00000038;
    display: inline-block;
    vertical-align: middle;
  }

  > i {
    display: inline-block;
    padding: 1rem;
    color: #fff;
    font-weight: normal;
    transition: color .3s ease;
  }

  a {
    font-size: 1.4rem;
    font-weight: normal;
    line-height: 1;
    display: block;
    padding: 1.2rem 3rem 1.2rem 1rem;
  }

  li {
    margin: 0;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 100%;
    left: 0;
    /*
    background-color: #108db8;
    */
    display: none;
    width: 120px;
    padding: 10px 0;
  }

  ul.show {
    display: inline-block;
  }

  @media only screen and (max-width: 920px) {
    display: inline;
  }
`

const Title = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  top: 50%;
  color: #fff;
  transform: translate(-50%,-50%);
  background-repeat: no-repeat;
  background-size: 100% auto;
  width: 10rem;
  height: 2.5rem
`



