import { markdown } from 'markdown'

export function markdownToStr(body = '') {
  return markdownToHTML(body).replace(/<(?!\/?(?=>|\s.*>))\/?.*?>/g, '')
}

export function markdownToHTML(body = '') {
  return markdown.toHTML(body)
}

export function sliceStrByCharNumber(str, size) {
  const chars = []
  let realLength = 0, len = str.length, charCode = -1;

  for (let i = 0; i < len; i++) {
    charCode = str.charCodeAt(i);
    if (charCode >= 0 && charCode <= 128)
      realLength += 1;
    else
      realLength += 2;

    chars.push(str[i])
    if (realLength >= size) break
  }

  return chars.join('') + '...'
}

// markdown字符串
export function markdownToText(body = '') {
  // size: 字符个数
  return (size = 140) => {
    const str = markdown.toHTML(body).replace(/<(?!\/?(?=>|\s.*>))\/?.*?>/g, '')
    const chars = []
    let realLength = 0, len = str.length, charCode = -1;

    for (let i = 0; i < len; i++) {
      charCode = str.charCodeAt(i);
      if (charCode >= 0 && charCode <= 128)
        realLength += 1;
      else
        realLength += 2;

      chars.push(str[i])
      if (realLength >= size) break
    }

    return chars.join('') + '...'
  }
}

export function backtop(e) {
  window.scroll(0, 0);
  // Remove hash from URL:
  history.pushState(
    "", document.title,
    window.location.pathname + window.location.search,
  );
  e.preventDefault();
}

export function isEn(str) {
  return !~escape(str).indexOf('%u')
}

export function normalizeTagName(name) {
  return name
}



