import React from 'react'

const getPos = src => src.split('#')[1] || 'center'
const getStyle = pos => ({
  display: 'block',
  margin: `0 ${pos === 'center' ? 'auto' : '0'}`,
  marginTop: '4rem',
  cursor: 'zoom-in'
})

export default ({src}) => (
  <img onClick={e => showImage(src)} style={getStyle(getPos(src))} src={src}/>
)

function showImage(src) {
  const el = document.createElement('div')
  el.style.zIndex = '9999'
  el.style.position = 'fixed'
  el.style.left = '0'
  el.style.top = '0'
  el.style.bottom = '0'
  el.style.right = '0'
  el.style.backgroundColor = '#fff'

  const img = new Image()
  img.src = src
  img.style.position = 'absolute'
  img.style.transform = 'translate(-50%, -50%)'
  img.style.left = '50%'
  img.style.top = '50%'
  img.style.maxHeight = '100%'
  img.style.maxWidth = '100%'

  img.style.cursor = 'zoom-out'
  el.appendChild(img)

  el.onclick = remove

  document.body.appendChild(el)
  document.addEventListener('scroll', remove)

  function remove() {
    document.body.removeChild(el)
    document.removeEventListener('scroll', remove)
  }
}



