import styled from 'styled-components'
import React from 'react'
import { withRouteData, Link } from 'react-static'
import groupBy from 'lodash.groupby'
import { markdownToText } from '../util'
import moment from 'moment'

export default withRouteData(({posts}) => {
  posts = posts.map(p => ({...p, d: p.date.split('-')[0]}))
  const grouped = groupBy(posts, 'd')
  return (
    <Root>
      {
        Object.keys(grouped).reverse().map(date => (
          <Wrapper key={date}>
            <header>
              <h3>{date}</h3>
            </header>
            <List>
              {
                grouped[date].map(post => (
                  <Post
                    to={post.url}
                    key={post.basename}
                  >
                    <header>
                      <h4>
                        {post.title}
                      </h4>
                    </header>
                  </Post>
                ))
              }
            </List>
          </Wrapper>
        ))
      }
    </Root>
  )
})

const Root = styled.div`
  margin: 2.3rem;
  @media only screen and (max-width: 825px) {
    margin: 2.3rem 0;
  }
`

const Wrapper = styled.div`
  margin-bottom: 6rem;

  > header {
    margin-bottom: 3rem;
  }

  header h3 {
    font-size: 3.2rem;
    color: rgb(154, 154, 154);
    letter-spacing: 0.15rem;
    font-weight: 300;
  }
`

const Post = styled(Link)`
  box-shadow: 2px 3px 7px rgba(0,0,0,.1);
  padding: 1rem;
  transition: box-shadow .2s ease;
  header h4 {
    font-size: 1.8rem; 
    color: #4a4a4a;
    font-weight: 500;
    margin: 0;
  }

  .post-content {
    color: #a0a0a0;
    font-weight: normal;
    font-size: 1.4rem;
  }

  &:hover {
    box-shadow: 3px 5px 10px rgba(0,0,0,.25);
    header h4 {
      color: #333;
    }
  }
`
const List = styled.section`
  display: grid;
  grid-template-columns: repeat(3, 300px);
  justify-content: center;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;

  @media only screen and (max-width: 1035px) {
    grid-template-columns: repeat(2, 360px);
  }

  @media only screen and (max-width: 825px) {
    grid-template-columns: 80%;
  }
`



