import React from 'react'
import { Link, withRouter } from 'react-static'
import styled from 'styled-components'

export default withRouter(({location: {pathname}, onSearch}) => {
  let matchCate = pathname.match(/\/cates\/([^\/]*)/)
  if (matchCate) matchCate = matchCate[1]

  return (
    <Root className="main-nav">
      <Link to="/" className="icon-link">
        <i className="iconfont icon-home"></i>
      </Link>
      {
        matchCate === 'frontend' ? (
          <Popular>
            <Link to="/tags/html">HTML</Link>
            <Link to="/tags/javascript">JS</Link>
            <Link to="/tags/css">CSS</Link>
          </Popular>
        ) : matchCate === 'backend' ? (
          <Popular>
            <Link to="/tags/java">Java</Link>
            <Link to="/tags/go">Go</Link>
            <Link to="/tags/nodejs">NodeJS</Link>
          </Popular>
        ) : matchCate === 'mobile' ? (
          <Popular>
            <Link to="/tags/flutter">Flutter</Link>
            <Link to="/tags/react_native">ReactNative</Link>
            <Link to="/tags/swift">Swift</Link>
          </Popular>
        ) : (
          <Popular>
            <Link to="/cates/frontend">前端</Link>
            <Link to="/cates/backend">后端</Link>
            <Link to="/cates/mobile">移动端</Link>
          </Popular>
        )
      }
      <Right>
        <Link data-tip="归档" data-for="tip-one" className="archive icon-link" to="/archives">
          <i className="iconfont icon-archive"></i>
        </Link>
        <a className="search" onClick={onSearch}>
          <i className="iconfont icon-search"></i>
        </a>
      </Right>
    </Root>
  )
})

const Root = styled.nav`
  &.fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    border-bottom: 1px solid #f1f1f1;
  }

  height: 50px;
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  background-color: #fff;

  &.showBorder {
    border-bottom: 1px solid #e7e7e7;
  }

  @media only screen and (max-width: 400px) {
    padding: 0 20px;
  }

  a {
    font-size: 1.6rem;
    font-weight: normal;
  }

  .icon-link {
    font-size: 1.8rem;
  }

  .icon-btn {
    -webkit-apparance: none;
    background-color: transparent;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
  }
`

const Popular = styled.span`
  @media only screen and (max-width: 920px) {
    display: none;
  }

  a {
    font-size: 1.5rem;
  }

  &::before {
    content: '';
    height: 2.1rem;
    width: 1px;
    background-color: #00000038;
    display: inline-block;
    vertical-align: middle;
  }
`

const Right = styled.div`
  margin-left: auto;
  display: flex;
  align-items: center;
  overflow: hidden;

  .search {
    cursor: pointer;
    margin-left: 10px;
    i { font-size: 20px; }
  }
`




