import React from 'react'

export default props => {
  let str = props.children[0]

  if (typeof str === 'string') {
    const [content, align] = str.split('_#_')
    
    if (align) {
      return (
        <p style={{textAlign: align}}>{content}</p>
      )
    }
  }

  return (
    <p>{props.children}</p>
  )
}



