import React from 'react'
import styled from 'styled-components'
import cx from 'classnames'
import PostContent from './post-content'
import {sliceStrByCharNumber, markdownToText} from '../util'

export default ({
  s,
  md,
  s1,
  s2,
  s3,
  lg,
  img,
  post = {}
}) => {
  return (
    <Root
      className={cx({
        's3-post': s3,
        's1-post': s1,
        's2-post': s2,
        's-post': s,
        'lg-post': lg,
        'md-post': md,
        'no-image': !img
      })}
    >
      <PostContent
        caption={{
          author: post.author,
          featured: !!lg,
          star: !lg,
          readingtime: 9,
          date: post.date
        }}
        title={post.title}
        summary={sliceStrByCharNumber(post.summary, 150)}
        large={!!lg}
        url={post.url}
      />
    </Root>
  )
}

const Root = styled.div`
  padding: 12px 0;

  &.lg-post {
    flex: 1 1 504px;
    grid-column-start: 2;
    grid-column-end: 8;
    grid-row-start: 1;
    grid-row-end: 2;
    padding: 0 0 24px 0;
    margin-right: 0;
  }

  &.s1-post, &.s2-post {
    @media screen and (max-width: 1031px) {
      padding: 0 0 0 24px;
    }
  }

  @media screen and (max-width: 1383px) {
    &.lg-post {
      grid-column-start: 1;
      grid-column-end: 7;
      .title-wrap {
        max-height: 120px;
      }
    }
  }

  @media screen and (max-width: 1207px) {
    &.lg-post {
      grid-area: 1/1/5/4;
      padding: 0;
    }
  }

  @media screen and (max-width: 1031px) {
    &.lg-post {
      grid-area: 1/1/5/6;
      padding: 0;
    }
  }

  @media screen and (max-width: 855px) {
    &.lg-post {
      grid-area: 1/1/5/5;
      padding: 0 12px;
    }

    &.s1-post, &.s2-post {
      width: 100%;
      padding: 12px;
    }
  }
}
`



