import { withRouter } from 'react-static'
import React from 'react'
import styled from 'styled-components'
import cx from 'classnames'
import Logo from '../logo.png'
import LogoWhite from '../codemore_white.png'

function getCustomClassName(pathname) {
  let matched
  if ((matched = pathname.match(/\/cates\/([^\/]*)/))) {
    matched = matched[1]
    return {
      [matched]: matched
    }
  }

  return {}
}

const Header = ({children, location}) => (
  <Root className={cx(getCustomClassName(location.pathname))}>
    {children}
  </Root>
)

export default withRouter(Header)

const Root = styled.div`
  position: absolute;
  width: 100%;
  &.fixed {
    position: fixed;
    background: #fff;
    top: -65px;
    z-index: 800;
    border-bottom: 1px solid #e1e1e1;
  }

  .logo {
    background-image: url(${Logo});
  }

  &.frontend, &.frontend > * {
    background-color: #108db8!important;
  }

  &.backend, &.backend > * {
    background-color: rgba(0, 100, 0, 1)!important;
  }

  &.mobile, &.mobile > * {
    background-color: rgb(219,112,147)!important;
  }

  &.mobile nav a,
  &.frontend nav a,
  &.backend nav a {
    color: #fff;
  }

  &.mobile .logo,
  &.frontend .logo,
  &.backend .logo {
    background-image: url(${LogoWhite});
  }
`



