import React from 'react'
import styled from 'styled-components'
import PostContent from './post-content'

export default ({index, post}) => (
  <Root>
    <div className="left">
      0{index}
    </div>
    <PostContent
      caption={{
        readingtime: post.readingtime,
        author: post.author,
        cate: post.tags.includes('Frontend') ? '前端' : post.tags.includes('Backend') ? '后端' : post.tags.includes('Mobile') ? '移动端' : '其他',
        date: post.date
      }}
      title={post.title}
      small
      className="post"
      url={post.url}
    />
  </Root>
)

const Root = styled.li`
  display: flex;
  margin-bottom: 24px;

  .title {
    max-height: 60px;
    -webkit-line-clamp: 3;
  }

  .post {
    margin-left: 16px;
  }

  .left {
    margin-top: 4px;
    color: rgba(0,0,0,.15);
    font-size: 34px;
  }

  .right {
    flex: 1 1 auto;
    margin-left: 16px;
    min-width: 200px;
    > a {
      flex: 0 0 auto;
      overflow: hidden;
      width: 100%;
      color: rgba(0,0,0,.84);
      h3 {
        margin-bottom: 4px;
        color: rgba(0,0,0,.84);
        letter-spacing: -.02em;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        transform: translateY(.96px);
        letter-spacing: -.17px;
        overflow: hidden;
        max-height: 60px;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
      }
    }
  }
`



