import React from 'react'
import { withRouteData, Link } from 'react-static'
import Markdown from 'react-markdown'
import universal from 'react-universal-component'
import styled from 'styled-components'
import moment from 'moment'
import StyledPost from '../components/post'
import '../github-markdown.css'
import '../qtcreator_dark.css'
import hlj from 'highlight.js'
import Helmet from 'react-helmet'
import { sliceStrByCharNumber, markdownToText } from '../util'
import AfterRender from '../components/after-render'
import CodeBlock from '../components/code-block'
import LinkBlock from '../components/link-block'
import ImageBlock from '../components/image-block'
import ParagraphBlock from '../components/paragraph-block'
import cx from 'classnames'

moment.locale('zh-cn')

export
const tag_name_map = {
  '前端': 'Frontend',
  '后端': 'Backend',
  '开发': 'Development',
  '移动端': 'Mobile',
  '跨平台': 'CrossPlatform'
}

export
function chineseToEnglish(name) {
  for(let [key, value] of Object.entries(tag_name_map)) {
    name = name.replace(key, value)
  }

  return name
}

export default withRouteData(({post}) => {
  const description = sliceStrByCharNumber(post.summary, 160)
  return (
    <StyledPost>
      <Helmet>
        <meta property="og:description" content={description} />
        <meta property="og:title" content={post.title} />
        <meta property="og:type" content="article" />
        <meta name="description" content={description} />
        <meta name="keywords" content={post.tags.join(',')} />
      </Helmet>
      <header className="post-header">
        <h1 className="post-title">{post.title}</h1>
        <section className={`post-meta `}>
          {
            post.author &&
            <a>{post.author}</a>
          }
          <time
            className={cx('post-date', {
              'has-author': post.author
            })}
            dateTime={post.date}>{moment(post.date).format('l')}</time>
          <span>, </span>
          <span>{post.tags.map((tag, i) => (
            <span key={tag}>
              <Link to={`/tags/${chineseToEnglish(tag)}`}>{tag}</Link>
              {
                i !== post.tags.length - 1 &&
                <span>, </span>
              }
            </span>
          ))}</span>
        </section>
        <div
          style={{
            maxWidth: 1000,
            maxHeight: 667,
            margin: '3rem 0'
          }}
        >
          <img style={{
            width: '100%', height: '100%',
            left: 0, top: 0,
            margin: 'auto'
          }} className="cover" src={post.cover} alt="" />
        </div>
      </header>
      <section className="post-content markdown-body">
        <Markdown
          source={post.body}
          transformImageUri={uri => uri.indexOf('http') !== 0 ? `http://p6jqy6mfr.bkt.clouddn.com/${uri}` : uri}
          renderers={{code: CodeBlock, link: LinkBlock, image: ImageBlock, paragraph: ParagraphBlock}}
        />
      </section>
      <section className="post-footer"></section>
      {
        (post.relatives && post.relatives.length) &&
        <Relatives>
          <h3>相关阅读:</h3>
          <ul>
            {
              post.relatives.map(({title, url}, i) => (
                <li key={i}>
                  <Link to={url}>{title}</Link>
                </li>
              ))
            }
          </ul>
        </Relatives>
      }
    </StyledPost>
  )
})

const Relatives = styled.div`
  position: fixed;
  top: 50%;
  transform: translate(0, -50%);
  right: 3rem;

  h3 {
    font-size: 16px;
    font-weight: 400!important;
  }

  ul {
    font-weight: 300!important;
    margin: 0;
    padding: 0;
    font-size: 12px;
    padding-left: 3rem;
    padding-top: .2rem;
  }

  li {
    max-width: 160px;
  }
`



