import React from 'react'
import { withRouteData } from 'react-static'
import styled from 'styled-components'
import PostContent from '../components/post-content'
import { sliceStrByCharNumber, isEn, markdownToText } from '../util'

export default withRouteData(({posts, cate}) => {
  return (
    <Root>
      <List>
        {
          posts.map((v, i) => (
            <PostContent
              key={i}
              caption={{
                readingtime: v.readingtime,
                date: v.date
              }}
              title={v.title}
              summary={sliceStrByCharNumber(v.summary, 320)}
              url={v.url}
              large
            />
          ))
        }
      </List>
    </Root>
  )
})

const Root = styled.div`
  width: 80%;
  max-width: 1000px;
  margin: 3rem auto;

  > div {
    margin-bottom: 3rem;
  }

`

const List = styled.div`
  display: grid;
  grid-template-columns: minmax(auto, 400px) minmax(auto, 400px);
  grid-row-gap: 3rem;
  grid-column-gap: 3rem;
  justify-content: space-between;
`



