import React from 'react'
import { Link, withRouteData } from 'react-static'
import styled from 'styled-components'
import {render} from 'react-dom'
import GridPost from '../components/grid-post'
import FeaturedItem from '../components/featured-item'
import ListItem from '../components/list-item'
import PopularItem from '../components/popular-item'
import shuffle from 'shuffle-array'

// 分页文章数
const page_size = 15

export default withRouteData(({others, featured, posts}) => (
  <Root>
    <div>
      <Featured>
        <div className="lg">
          <GridPost
            lg
            img
            post={featured[0]}
          />
          <GridImage
            className="grid-post-image under-lg"
            style={{
              backgroundImage: `url(${featured[0].cover})`,
              backgroundPosition: '50% 39%'
            }}
            to={featured[0].url}
          />
        </div>
        <div className="s1 s">
          <GridImage className="grid-post-image under-s under-s1"
            style={{
              backgroundImage: `url(${featured[1].cover})`,
              backgroundPosition: '37% 38%'
            }}
            to={featured[1].url}
          />
          <GridPost
            s
            s1
            img
            post={featured[1]}
          />
        </div>
        <div className="s2 s">
          <GridImage className="grid-post-image under-s under-s2"
            style={{
              backgroundImage: `url(${featured[2].cover})`,
              backgroundPosition: '67% 50%'
            }}
            to={featured[2].url}
          />
          <GridPost
            s
            s2
            img
            post={featured[2]}
          />
        </div>
        <div className="s3 s">
          <GridImage className="grid-post-image under-s under-s3"
            style={{
              backgroundImage: `url(${featured[3].cover})`,
              backgroundPosition: '67% 50%'
            }}
            to={featured[3].url}
          />
          <GridPost
            s
            s3
            img
            post={featured[3]}
          />
        </div>
        <div className="cp">
          <div className="border-top"></div>
          {
            featured.slice(5, 7).filter(v => v).map((v, i) => (
              <FeaturedItem
                key={i}
                post={v}
              />
            ))
          }
          <SeeAll>
            <a href="#">
              查看所有精选
              <span>
                <svg width="19" height="19" viewBox="0 0 19 19"><path d="M7.6 5.138L12.03 9.5 7.6 13.862l-.554-.554L10.854 9.5 7.046 5.692" fillRule="evenodd"></path></svg>
              </span>
            </a>
          </SeeAll>
        </div>
        <div className="md">
          <GridImage className="grid-post-image under-s under-md"
            style={{
              backgroundImage: `url(${featured[4].cover})`,
              backgroundPosition: '67% 50%'
            }}
            to={featured[4].url}
          />
          <GridPost
            s
            md
            img
            post={featured[4]}
          />
        </div>
      </Featured>
    </div>
    <div style={{marginBottom: 60}}></div>
    <FeedContainer>
      <Feeds>
        <ListWrapper>
          {
            others.map((v, i) => (
              <ListItem
                key={i}
                post={v}
              />
            ))
          }
        </ListWrapper>
        <SidebarWrapper>
          <div className="sidebar-wrapper">
            <div className="sidebar">
              <div>
                <aside className="popular">
                  <a href="">
                    <div>
                      <h3>热门文章</h3>
                    </div>
                  </a>
                  <ol>
                    {
                      shuffle(posts).slice(0, 4).map((v, i) => (
                        <PopularItem key={i} post={v} index={i + 1} />
                      ))
                    }
                  </ol>
                </aside>
              </div>
            </div>
            <footer></footer>
          </div>
        </SidebarWrapper>
      </Feeds>
    </FeedContainer>
  </Root>
))

const Root = styled.div``

const Featured = styled.section`
  display: grid;
  max-width: 1384px;
  width: 100%;
  margin: 0 auto;
  margin-top: 12px;
  height: 548px;
  grid-gap: 24px;
  grid-auto-columns: 64px;
  grid-auto-rows: 28px;

  .lg {
    display: grid;
    grid-area: lg;
    grid-template-columns: repeat(7, 64px);
    grid-template-rows: max-content 1fr;
    grid-column-gap: 24px;
    height: 100%;
    .grid-post-image {
      width: auto;
      height: 100%;
      flex: 0 0 auto;
    }
  }

  .s {
    flex-grow: 1;
    height: auto;
  }

  .s1 {
    grid-area: s1;
  }

  .s2 {
    grid-area: s2;
  }

  .cp {
    grid-area: cp;
    .border-top {
      border-top: 2px solid rgba(0, 0, 0, .84);
    }
  }

  .md {
    grid-area: md;
  }

  .s3 {
    grid-area: s3;
  }

  @media screen and (max-width: 1383px) {
    max-width: 1208px;
    grid-template-columns: repeat(12, 64px);
    grid-template-rows: repeat(11, 28px);
    grid-template-areas: "lg lg lg lg lg lg s1 s1 s1 s1 s2 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s1 s2 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s1 s2 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s1 s2 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s1 s2 s2 s2 s2" "lg lg lg lg lg lg cp cp md md md s3 s3 s3" "lg lg lg lg lg lg cp cp md md md s3 s3 s3" "lg lg lg lg lg lg cp cp md md md s3 s3 s3" "lg lg lg lg lg lg cp cp md md md s3 s3 s3" "lg lg lg lg lg lg cp cp md md md s3 s3 s3" "lg lg lg lg lg lg cp cp md md md s3 s3 s3";

    .lg {
      grid-template-columns: repeat(6, 64px);
    }
  }

  @media screen and (max-width: 1207px) {
    max-width: 1032px;
    grid-template-rows: repeat(11, 28px);
    grid-template-areas: "lg lg lg lg lg lg s1 s1 s1 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s2 s2 s2" "lg lg lg lg lg lg s1 s1 s1 s2 s2 s2" "md md md md md md cp cp cp s3 s3 s3" "md md md md md md cp cp cp s3 s3 s3" "md md md md md md cp cp cp s3 s3 s3" "md md md md md md cp cp cp s3 s3 s3" "md md md md md md cp cp cp s3 s3 s3" "md md md md md md cp cp cp s3 s3 s3";
  }

  @media screen and (max-width: 1031px) {
    max-width: 856px;
    grid-template-rows: repeat(13, 28px);
    grid-template-columns: repeat(10, 64px);
    grid-template-areas: "lg lg lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg lg lg" "s1 s1 s1 s1 s1 s2 s2 s2 s2 s2" "s1 s1 s1 s1 s1 s2 s2 s2 s2 s2" "s1 s1 s1 s1 s1 s2 s2 s2 s2 s2" "md md md md s3 s3 s3 cp cp cp" "md md md md s3 s3 s3 cp cp cp" "md md md md s3 s3 s3 cp cp cp" "md md md md s3 s3 s3 cp cp cp" "md md md md s3 s3 s3 cp cp cp" "md md md md s3 s3 s3 cp cp cp";
    height: auto;

    .lg {
      grid-template-columns: repeat(10, 64px);
    }

    .s1, .s2 {
      display: flex;
      height: 100%;
    }
  }

  @media screen and (max-width: 855px) {
    max-width: 680px;
    grid-template-columns: repeat(8, 1fr);
    grid-auto-rows: 28px;
    height: auto;
    grid-template-rows: repeat(10, 28px);
    grid-template-areas: "lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg" "lg lg lg lg lg lg lg lg" "s1 s1 s1 s1 s2 s2 s2 s2" "s1 s1 s1 s1 s2 s2 s2 s2" "s1 s1 s1 s1 s2 s2 s2 s2" "s1 s1 s1 s1 s2 s2 s2 s2" "s1 s1 s1 s1 s2 s2 s2 s2";
    .cp, .md, .s3 {
      display: none;
    }

    .lg {
      grid-template-columns: repeat(8, 1fr);
      max-width: 100%;
    }

    .s3, .cp, .md {
      display: none;
    }

    .s1, .s2 {
      flex-direction: column;
    }
  }
`

const GridImage = styled(Link)`
  display: block;
  background-size: cover;

  &.under-s {
    height: 104px;
  }

  &.under-s1, &.under-s2 {
    @media screen and (max-width: 1031px) {
      width: 152px;
      height: 100%;
      flex: 0 0 auto;
    }

    @media screen and (max-width: 855px) {
      margin: 0;
      order: -1;
      height: 104px;
      width: 100%;
    }
  }

  &.under-md,
  &.under-s3 {
    @media screen and (max-width: 1383px) {
      height: 140px;
      flex: 0 0 auto;
    }
  }

  &.under-lg {
    height: 100%;
    flex: 0 0 auto;
    grid-column-start: 1;
    grid-column-end: 8;
    grid-row-start: 2;
    grid-row-end: 3;

    @media screen and (max-width: 1383px) {
      grid-column-start: 1;
      grid-column-end: 7;
    }

    @media screen and (max-width: 1207px) {
      grid-area: 1/4/5/7;
    }

    @media screen and (max-width: 1031px) {
      grid-area: 1/6/5/11;
    }

    @media screen and (max-width: 855px) {
      grid-area: 1/5/5/9;
      height: 100%;
    }
  }
`

const SeeAll = styled.div`
  line-height: 20px;
  font-size: 16px;
  margin-top: 12px;
  margin-bottom: 12px;
  a { font-weight: 400; }

  a > span {
    vertical-align: middle;
    transform: translateY(-1.33px);
    display: inline-block;
    line-height: 0;
  }

`

const FeedContainer = styled.div`
  min-height: 100vh!important;
`
const Feeds = styled.section`
  max-width: 1032px;
  width: 100%;
  margin: 0 auto;
  margin-top: 3rem;
  display: flex;

  @media screen and (max-width: 1031px) {
    padding: 16px;
  }
`

const ListWrapper = styled.div`
  max-width: 680px;
  width: 100%;
  padding-right: 24px;
  @media screen and (max-width: 855px) {
    max-width: 100%;
    padding: 0;
  }
`

const SidebarWrapper = styled.div`
  flex: 0 0 auto;
  width: 328px;
  > div {
    width: 328px;
    &.fixed {
      position: fixed;
      top: 50px;
    }
  }

  .sidebar > div{
    margin-bottom: 40px;
  }

  aside.popular {
    line-height: 20px;
    font-size: 16px;

    a div {
      padding-bottom: 16px;
      margin-right: 32px;
      margin-left: 32px;
      justify-content: space-between;
      display: flex;
      align-items: center;
      border-bottom: 1px solid rgba(0,0,0,.15);
      h3 {
        margin: 0;
        padding-top: 20px;
        color: rgba(0,0,0,.84);
        letter-spacing: -.02em;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        transform: translateY(-.72px);
        letter-spacing: -.42px;
        font-weight: 600;
      }
    }

    ol {
      padding: 32px;
    }
  }
`



