import React from 'react'
import styled from 'styled-components'
import PostContent from './post-content'

export default ({post}) => (
  <Root
    caption={{
      author: post.author,
      star: true
    }}
    title={post.title}
    small
    url={post.url}
  />
)

const Root = styled(PostContent)`
  padding-top: 12px;
  &::after {
    content: '';
    display: block;
    border-top: 1px solid rgba(0, 0, 0, .15);
    width: 64px;
    margin-top: 12px;
  }
`



