import React from 'react'
import { Link } from 'react-static'
import styled from 'styled-components'
import PostContent from './post-content'
import { markdownToText, sliceStrByCharNumber } from '../util'

export default ({post}) => (
  <Root>
    <PostContent
      caption={{
        author: post.author,
        readingtime: post.readingtime,
        cate: post.tags.includes('Frontend') ? '前端' : post.tags.includes('Backend') ? '后端' : post.tags.includes('Mobile') ? '移动端' : '其他',
        date: post.date
      }}
      title={post.title}
      summary={sliceStrByCharNumber(post.summary, 150)}
      className="post"
      url={post.url}
    />
    <div className="image">
      <Link to={post.url} style={{
        backgroundImage: `url(${post.cover})`
      }}></Link>
    </div>
  </Root>
)

const Root = styled.div`
  display: flex;
  align-items: stretch;
  flex: 1 1 auto;
  max-width: 680px;
  width: 100%;
  line-height: 20px;
  font-size: 16px;
  margin-bottom: 48px;

  .summary-wrap {
    margin-top: 2px;
  }

  @media screen and (max-width: 855px) {
    max-width: 100%;
  }

  .post {
    margin-right: 24px;
    flex: 1;
  }

  .image {
    width: 152px;
    flex: 0 0 auto;
    a {
      background-position: center;
      background-origin: border-box;
      background-size: cover;
      background-position: 50% 50%;
      width: 100%;
      height: 100%;
      display: block;
    }
  }
`



