import React from 'react'
import { withRouter, Link, withRouteData } from 'react-static'
import styled, { keyframes } from 'styled-components'
import cx from 'classnames'

const Search = ({
  close,
  searchByTag,
  searchByKeyword,
  tags,
  onSearchInputChange,
  search_match_tags,
  activeItem,
  onSearchInputKeyUp,
  history
}) => (
  <Root>
    <div className="input">
      <i className="iconfont icon-search"></i>
      <input
        type="text"
        placeholder="输入关键字.."
        autoFocus
        onChange={e => onSearchInputChange(e.target.value, tags)}
        onKeyUp={e => onSearchInputKeyUp(e, () => {
          close()
          history.push(`/tags/${search_match_tags[activeItem]}`)
        }, () => {
          close()
          history.push(`/search?q=${e.target.value.trim()}`)
        })}
      />
      {
        search_match_tags && (
          <ul>
            {
              search_match_tags.map((v, i) => (
                <li
                  key={i}
                  onClick={close}
                  className={cx({
                    active: activeItem === i
                  })}
                >
                  <Link to={`/tags/${v}`}>{v}</Link>
                </li>
              ))
            }
          </ul>
        )
      }
    </div>
    <button className="close" onClick={close}>
      <i className="iconfont icon-close"></i>
    </button>
  </Root>
)

export default withRouter(withRouteData(Search))

const fadeIn = keyframes`
  0% {
    opacity: 0;
    bottom: 100%;
  }

  100% {
    opacity: 1;
    bottom: 0;
  }
`

const Root = styled.div`
  position: fixed;
  background-color: rgba(255, 255, 255, .82);
  z-index: 1500;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  animation: ${fadeIn} .35s ease;

  .iconfont {
    font-size: 24px;
  }

  .input {
    margin: 10rem auto;
    width: 80%;
    max-width: 900px;
    border-bottom: 2px solid #333;
    display: flex;
    align-items: center;
    position: relative;
  }

  input {
    flex: 1;
    background-color: transparent;
    -webkit-appearance: none;
    outline: none;
    border: none;
    height: 54px;
    font-size: 20px;
    padding: 0 1rem;
  }

  .close {
    position: absolute;
    top: 0;
    right: 0;
    -webkit-appearance: none;
    background-color: transparent;
    border: none;
    outline: none;
    width: 54px;
    height: 54px;
    right: 2.4rem;
    top: 2.4rem;
    cursor: pointer;
    i {
      font-size: 40px;
    }
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #fff;
    position: absolute;
    top: 100%;
    width: 100%;
    left: 0;
    right: 0;
    margin-top: 1px;
    max-height: calc(100vh - 10rem - 54px - 15px);
    overflow-y: auto;

    li {
      margin: 0;
      &.active,
      &:hover {
        background-color: rgba(204, 204, 204, .4);
      }

      a {
        display: block;
        padding: 10px 35px;
      }
    }
  }
`



